
.card-wrapper {
  display: inline-block;
  height: 24px;
  line-height: 24px;
  font-size: 0;
}
.card-wrapper > .decrease {
  vertical-align: top;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  -webkit-transform: translate3D(0, 0, 0);
          transform: translate3D(0, 0, 0);
}
.card-wrapper > .decrease > .inner {
  display: inline-block;
  width: 20px;
  height: 20px;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  -webkit-transform: rotate(0);
          transform: rotate(0);
}
.card-wrapper > .decrease.fade-enter,
.card-wrapper > .decrease.fade-leave-active {
  opacity: 0;
  -webkit-transform: translate3D(12px, 0, 0);
          transform: translate3D(12px, 0, 0);
}
.card-wrapper > .decrease.fade-enter > .inner,
.card-wrapper > .decrease.fade-leave-active > .inner {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.card-wrapper > p {
  font-size: 12px;
  display: inline-block;
  color: #93999f;
  width: 24px;
  height: 24px;
  text-align: center;
  line-height: 24px;
}
.card-wrapper > span {
  display: inline-block;
  line-height: 24px;
  font-size: 0;
  vertical-align: top;
}
.card-wrapper > span > .icon-add_circle,
.card-wrapper > span > .icon-remove_circle_outline {
  font-size: 20px;
  line-height: 24px;
  color: #1aa1d9;
}


